<script>
import { getDomainType } from "@/utils/Validator";
import { dateFormat } from "@/utils";
export default {
  name: "DomainCard",
  components: {},
  data() {
    return {};
  },
  props: {
    item: {
      default: [],
      type: Array,
      required: true,
    },
  },
  methods: {
    gotoWhois() {
      this.$emit("gotoWhois", this.item.domaintext);
    },
  },
  computed: {
    // ...Vuex.mapState({
    //   topItems: (state) => {
    //     return state.assets.latestRoots || [];
    //   },
    //   subItems: (state) => {
    //     return state.assets.lastestSubs || [];
    //   },
    // }),
    expireDate() {
      if (this.item.expire) {
        return dateFormat(this.item.expire);
      }
      return "";
    },
    i18nType() {
      const name = this.item.domaintext;
      return name ? this.$t(`g.${getDomainType(name)}`) : "";
    },
  },

  mounted() {},
};
