  //   // .flex-column-between-start();

  //   &-item:not(:last-child) {
  //     cursor: pointer;
  //     // .flex-row-start-center();
  //     margin-bottom: 20px;
  //     span {
  //       margin-left: 10px;
  //     }
  //   }
  //   &-item:last-child {
  //     cursor: pointer;
  //     span {
  //       margin-left: 10px;
  //     }
  //   }
  // }
  .before {
    opacity: 0;
    transform: scale(0, 0);
    transform-origin: 80% 0;
  }

  .enter {
    opacity: 1;
    transform: scale(1, 1);
    transform-origin: 80% 0;
    transition: opacity 443ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
      transform 462ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  }

  .card-m {
    position: relative;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    padding: 12px 0;
    aspect-ratio: 16 / 9;
    background: linear-gradient(90deg, #2e89e2 0%, #42d7ec 100%);

    .dm {
      width: 100%;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-size: 40px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: #fff;
